body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding: 0;
  margin: 0;
}

body {
  background-color: #fff;
  color: #555;
  /*Add font family*/
  font-size: 20px;
  font-weight: 300;
  text-align: center;
}

h1 {
  color: rgb(211,211,211);
  word-spacing: 3px;
  letter-spacing: 1px;
  font-weight: 400;
  font-size: 400%;
  padding-top: 12%;
}

h2 {
  padding-top: 10px;
  font-weight: 400;
  font-size: 24px;
}

.section-header h2 {
  color: rgb(211,211,211);
  font-size: 24px;
}


.section-header {
  background-image: linear-gradient(rgb(0,0,0,0.8), rgb(0,0,0,0.8)), url(/my-website/static/media/balijungle1.68a2a9af.jpg);
  background-position: bottom;
  background-attachment: fixed;
  background-size: cover;
  height: 100vh;
}

.section-about {
  height: 100vh;
}

.section-myprojects {
  color: #fff;
  height: 100vh;
  background-color: rgb(82, 184, 99);
}

.section-contactme {
  height: 100vh;
  background-color: #fff;
}

.section-footer {
  height: 25vh;
  color:#fff;
  background-color: #555;
}

.navi {
  padding-top: 20px;
  padding-left: 50%;
}

h3 {
  display: inline;
  padding: 10px;
  font-weight: 100;
  color: #fff; 
}


@media only screen and (max-width: 620px) {
  h1 {
    font-size: 50px;
    padding-top: 30%;
  }

  h2 {
    font-size: 15px;
    font-weight: 400;
  }
}
